.footer .footerContent{
    display: flex;
    gap: 20px;
    margin-bottom: 24px;
}
.footer .list_wrap{
    display: flex;
    gap: 180px;
    line-height: 40px;
}
.footer .list_wrap li a{
    color: #808080;
}
.footer .list_wrap li:hover a{
    color: #fff;
    text-decoration: underline;
}
.footer .btn button{
    padding: 5px 10px;
    border: 1px solid #808080;
    color: #808080;
    margin-top: 40px;
}
.footer .btn button:hover{
    background: #000;
    color: #fff;
}
.footer p{
    margin-top: 35px;
    color: #808080;
    font-size: 14px;
}