.allMovie{
    width: 100%;
    padding: 60px 0 0 60px;
}
.allMovie h2{
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
}
.allMovie .posters{
    display: flex;
    gap: 10px;
}
.allMovie .posters img{
    width: 100%;
    /* 이미지 왜곡 잡기 */
    object-fit: contain;
    max-width: 200px;
    transition: all 0.2s ease-in-out;
}
.allMovie .posters img:hover{
    transform: scale(1.1);
}