.header{
    width: 100%;
    height: 70px;
    display: flex;
    position: fixed;
    left: 0;
    top: 0;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    z-index: 100;
}
.header .hgnb_wrap{
    display: flex;
    gap: 45px;
    align-items: center;
}
.header .hgnb_wrap .logo{
    width: 90px;
    cursor: pointer;
}
.header .hgnb_wrap .gnb{
    display: flex;
    gap: 20px;
}
.header .avatar{
    width: 32px;
    border-radius: 4px;
}
.icon_wrap{
    display: flex;
    gap: 10px;
    align-items: center;
}
.icon_wrap div{
    cursor: pointer;
}
.header_black{
    background: #141414;
    transition: all 0.5s ease-in-out;
}