.banner{
    width: 100%;
    height: 100vh;
    /* 이미지 채우기 */
    object-fit: contain;
    position: relative;
}
.banner .contents{
    position: absolute;
    left: 70px;
    bottom: 100px;
}
.banner .contents .title h1{
    font-size: 50px;
    font-weight: bold;
}
.banner .contents .btn_wrap{
    margin: 40px 0 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}
.banner .contents .btn_wrap button{
    background: #fff;
    padding: 10px 30px;
    font-size: 20px;
    font-weight: bold;
    transition: all 0.5s ease-in-out;
    border-radius: 4px;
}
.banner .contents .btn_wrap .play{
    background: #fff;
    color: #141414;
}
.banner .contents .btn_wrap .play:hover{
    color: #fff;
    background: rgba(114, 106, 114, 0.6);
}
.banner .contents .btn_wrap .info{
    color: #fff;
    background: rgba(114, 106, 114, 0.6);
}
.banner .contents .btn_wrap .info:hover{
    background: #fff;
    color: #141414;
}
.banner .contents .description{
    width: 750px;
    line-height: 1.5;
    font-weight: bold;
    font-size: 20px;
}